.coloring{

  &__container{
    background-image: url('../img/bg-main.jpg');
    background-size: cover;
    min-height: 800px;
    padding-bottom: 200px;
  }

  &__inner{
    margin: 0 auto;
  }
  &__title{
    margin: 0 auto;
    padding-top: 50px;
    max-width: 1000px;
    font-family: 'sans-serif';
    font-style: italic;
    font-weight: 400;
    font-size: 40px;
    font-family: 'sans-serif';
    font-style: italic;
    text-align: center;
    margin-bottom: 20px;
  }
  &__subtitle{
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    font-family: 'sans-serif';
    background-image: url('../img/decor-1.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    max-width: 600px;
    height: 70px;
    font-family: 'sans-serif';
    font-style: italic;
    text-align: center;
  }
  &__list{
    margin: 0 auto;
    max-width: 1000px;
    display: flex;
    justify-content: space-between;
  }
  &__item{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 320px;
    border-radius: 5px;
    border-bottom: dashed 1px #006699;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
    padding: 10px 15px;
    transition:  transform 0.5s ease-in;

    @include hover{
      transform: translateY(-10px);
    }
  }
  &__descr{
    margin: 0;
    font-size: 16px;
    font-weight: 600;
    text-align: center;
  }
  &__img{
    width: 100px;
    height: 100px;
  }
  .swiper {
    margin-top: 50px;
    width: 1000px;
    height: 600px;

  }
  .swiper-slide{
    display: flex;
    gap: 40px;
  }
  .swiper-img-box{
    background-size: cover;
    border-radius: 5px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
    width: 50%;
    height: 550px;
    border-radius: 8px;
  }
  .swiper-text{
    max-width: 500px;
    font-weight: 400;
    font-size: 22px;
    font-weight: 500;
    line-height: 28px;
    span{
      font-size: 30px;
      font-weight: 700;
    }
  }
  .swiper-btns{
    position: absolute;
    width: 100px;
    bottom: 0px;
    right: 30px;
  }
  .swiper-button-prev{
    position: absolute;
    top: 85%;
    left: 500px;
    background-color: #D9E454;
    width: 50px;
    height: 50px;
    background-image: url('../img/svg/next.svg');
    background-repeat: no-repeat;
    background-position: center;
    z-index: 100;

    &::after{
      display: none;
    }
  }
  .swiper-button-next{
    position: absolute;
    z-index: 100;
    top: 85%;
    left: 560px;
    border: none;
    background-color: #D9E454;
    width: 50px;
    height: 50px;
    background-image: url('../img/svg/prev.svg');
    background-repeat: no-repeat;
    background-position: center;

  &::after {
    display: none;
    }
  }

}

@include tablet{
  .coloring__container{
    padding-bottom: 50px;
  }
  .coloring__title{
    margin: 0 auto;
    max-width: 700px;
    padding-top: 30px;
    font-size: 30px;
    margin-bottom: 10px;
  }
  .coloring__subtitle{
    font-size: 20px;
    font-family: 'sans-serif';
    background-image: none;
    max-width: 600px;
    height: 30px;
  }
  .swiper {
    margin-top: 50px;
    width: 400px !important;
    height: 870px !important;
    margin-bottom: 30px;
  }
  .swiper-slide{
    display: flex !important;
    flex-direction: column !important;
  }
  .swiper-img-box{
    width: 400px !important;
    height: 400px !important;
    border-radius: 8px;
  }
  .swiper-text{
    font-size: 16px !important;
    font-weight: 500;
    line-height: 25px !important;
    span{
      font-family: 'sans-serif';
      font-style: italic;
      font-size: 26px !important;
      font-weight: 700;
    }
  }
  .swiper-button-prev{
    top: 820px !important;
    left: 280px !important;
  }
  .swiper-button-next{
    top: 820px !important;
    left: 350px !important;
  }
  .coloring__list{
   flex-direction: column;
   align-items: center;
   gap: 20px;
  }
}
@include mobile {
  .swiper {
    margin-top: 30px;
    width: 300px !important;
    height: 770px !important;
    margin-bottom: 30px;
  }
  .swiper-img-box{
    width: 300px !important;
    height: 300px !important;
    border-radius: 8px;
  }
  .swiper-text{
    margin: 0;
  }
  .swiper-button-prev{
    top: 150px !important;
    left: 0px !important;
    width: 30px !important;
    height: 30px !important;
  }
  .swiper-button-next{
    width: 30px !important;
    height: 30px !important;
    top: 150px !important;
    left: 270px !important;
  }
}
