.header{
  position: absolute;
  width: 100%;
  z-index: 100;
  top: 0;
  left: 0;

  &__inner{
    margin: 0 auto;
    max-width: 1300px;
    padding-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo-box{
    background-image: url('../img/decor.png');
  }
  &__logo{
    margin: 0;
    font-family: 'DancingScript';
    font-size: 40px;
    font-weight: 700;
    color: #555555;
  }
  &__list{
    display: flex;
    align-items: center;
    gap: 30px;
  }
  &__link{
    font-size: 30px;
    color: #555555;
    transition:  color 0.5s ease-in;

    @include hover{
      color:#D9E454;
    }
  }
  &__adress-box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__social-box{
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &__adress{
    max-width: 250px;
    font-size: 14px;
    display: flex;
    align-items: center;
    gap: 5px;
  }
  &__text{
    margin: 0;
    font-size: 14px;
  }
  &__place{
    margin: 0;
    font-size: 12px;
    line-height: 12px;
  }
  &__location{
    fill: #555555;
    width: 40px;
    height: 40px;
  }
  &__phone{
    font-size: 22px;
    color: #555555;
    font-weight: 600;
    transition:  color 0.5s ease-in;

    @include hover{
      color:#D9E454;
    }
  }
}
@media (max-width: (1250px)){
  .header__logo{
    font-size: 30px;
  }
  .header__link{
    font-size: 20px;
  }
  .header__phone{
    font-size: 18px;
  }
}
@include tablet {
  .header__inner{
    flex-direction: column;
    gap: 10px;
  }
  .header__adress{
    margin-left: 20px;
}
}
@include mobile {
  .header__list{
    gap: 10px;
  }
  .header__link{
    font-size: 16px;
  }
}
