.footer{
  &__container{
    padding: 30px;
    background-image: url('../img/bg-main.jpg');
    background-size: cover;
  }
  &__inner{
    margin: 0 auto;
    max-width: 1300px;
    padding-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo-box{
    background-image: url('../img/decor.png');
  }
  &__logo{
    margin: 0;
    font-family: 'DancingScript';
    font-size: 40px;
    font-weight: 700;
    color: #555555;
  }
  &__list{
    display: flex;
    align-items: center;
    gap: 30px;
  }
  &__link{
    font-size: 30px;
    color: #555555;
    transition:  color 0.5s ease-in;

    @include hover{
      color:#D9E454;
    }
  }
  &__adress-box{
    max-width: 200px;
    display: flex;
    flex-direction: column;
  }
  &__adress{
    display: flex;
    align-items: center;
    gap: 5px;
  }
  &__place{
    margin: 0;
    font-size: 12px;
    line-height: 12px;
  }
  &__location{
    fill: #555555;
    width: 20px;
    height: 40px;
  }
  &__phone{
    font-size: 20px;
    color: #555555;
    line-height: 20px;
  }
}
@include small-tablet{
  .footer__inner{
    flex-direction: column;
    gap: 20px;
  }
  .footer__list{
    flex-direction: column;
    gap: 10px;
  }
}
