.contacts{

  &__container{
    background-image: url('../img/bg-main.jpg');
    background-size: cover;
  }
  &__inner{
    margin: 0 auto;
    display: flex;
    max-width: 1380px;
    padding-bottom: 50px;
    gap: 50px;
  }
  &__title{
    margin: 0;
    padding-top: 40px;
    font-family: 'sans-serif';
    font-style: italic;
    font-weight: 400;
    font-size: 40px;
    font-family: 'sans-serif';
    font-style: italic;
    text-align: center;
    margin-bottom: 20px;
  }
  &__map{
    max-width: 620px !important;
    filter: brightness(0.9);
    border-radius: 5px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
    border-radius: 8px;
  }
  &__adress{
    height: 120px;
    display: flex;
    gap: 20px;
    align-items: center;
    max-width: 620px;
    border-radius: 5px;
    border-bottom: dashed 1px #006699;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
    padding: 10px 15px;
    margin-bottom: 41px;
    transition:  transform 0.5s ease-in;

    @include hover{
      transform: translateY(-10px);
    }
  }
  &__location{
    display: block;
    width: 100px;
    height: 100px;
    fill: #D9E454;

    &-phone{
      height: 80px;
      width: 80px;
    }
  }
  &__icon{
    display: block;
    width: 80px;
    height: 80px;
    fill: #D9E454;
  }
  &__text{
    font-size: 26px;
    font-weight: 500;
    color: var(--text-xolor);
  }
  &__adress-box{
    height: 120px;
    display: flex;
    gap: 10px;
    align-items: center;
    max-width: 620px;
    border-radius: 5px;
    border-bottom: dashed 1px #006699;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
    padding: 10px 15px;
    margin-bottom: 41px;
    transition:  transform 0.5s ease-in;

    @include hover{
      transform: translateY(-10px);
    }
  }
  &__phone-box{
    height: 120px;
    display: flex;
    gap: 20px;
    align-items: center;
    max-width: 620px;
    border-radius: 5px;
    border-bottom: dashed 1px #006699;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
    padding: 10px 15px;
    transition:  transform 0.5s ease-in;

    @include hover{
      transform: translateY(-10px);
    }
  }
}
@include tablet {
  .contacts__inner{
    flex-direction: column;
    align-items: center;
  }

  .contacts__text{
    font-size: 20px;
    max-width: 250px;
  }
  .contacts__location{
    width:  70px;
    height: 70px;
  }
  .contacts__location-phone{
    width: 60px;
    height: 60px;
  }
  .contacts__icon{
    width: 70px;
    height: 70px;
  }

}
