.hero{

  &__container{
    background-image: url('../img/bg-main.jpg');
    background-size: cover;
  }
  &__inner{
    margin: 0 auto;
    max-width: 1300px;
    position: relative;
    min-height: 780px;
    padding-top: 150px;
    display: flex;
    gap: 30px;
  }

   &__right{
    position: relative;
  }
  &__img{
    width: 280px;
    height: 470px;
    position:absolute ;
    bottom: 0;
    left: 180px;
  }
  &__text{
    margin: 0;
    font-family: 'sans-serif';
    font-style: italic;
    font-size: 20px;
    font-weight: 500;
    padding-left: 55px;
    margin-bottom: -15px;
  }
  &__title{
    margin: 0;
    font-size: 50px;
    font-family: 'sans-serif';
    font-style: italic;
    background:linear-gradient(128.81deg, #7ECAA6 2.43%, #61BDD1 28.17%, #CF6DDD 53.17%, #4061F8 75.98%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  &__subtitle{
    margin: 0;
    font-family: 'sans-serif';
    font-style: italic;
    padding-left: 55px;
    font-size: 20px;
    font-weight: 500;
    margin-top: -5px;
    margin-bottom: 20px;
    margin-left: 250px;
  }
  &__descr{
    margin: 0;
    margin-top: -30px;
    padding: 0px 10px;
    max-width: 296px;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('../img/decor-2.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0.015em;
    text-align: center;
    font-family: 'sans-serif';
    font-style: italic;
  }
  &__btn{
    display: block;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 26px;
    color: var(--text-color);
    background-image: url('../img/decor-1.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    max-width: 600px;
    height: 70px;
  }
  &__name{
    margin: 0;
    font-family: 'sans-serif';
    font-style: italic;
    font-size: 40px;
    font-weight: 500;
    margin-left: 55px;
    padding-bottom: 10px;
  }

  .accordion{
    max-width: 600px;

    &__control {
      text-align: left;
      width: 100%;
      background-color: transparent;
      cursor: pointer;
      font-size: 20px;
      line-height: 20px;
      color: #242424;
      }

    &__content {
      font-weight: 400;
      font-size: 20px;
      color: #000;
      max-height: 0;
      opacity: 0;
      overflow: hidden;
      padding: 0px 10px;
      will-change: max-height;
      transition: all 0.3s ease-out;
      box-sizing: content-box;
    }
    &__title {
      margin: 0;
      padding: 5px;
      font-weight: 400;
      font-size: 26px;
      font-weight: 500;
      letter-spacing: 0em;
      text-align: left;
      color: #2F4550;
    }

    &__descr{
      margin: 0;
      font-size: 20px;
      font-weight: 350;
      line-height: 30px;
      letter-spacing: 0em;
      text-align: left;
      color: #2F4550;
      text-decoration: underline;

      span{
        font-weight: 700;
      }
    }
    &__about{
      margin: 0;
      font-size: 22px;
      padding: 10px 0px;

    }
    &__link{
      margin-top: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 10px;
      font-size: 16px;
      width: 150px;
      height: 40px;
      box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
      background-color: #D9E454;
      color: var(--light-color);
    }
    &__price{
      margin-top: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 10px;
      font-size: 14px;
      width: 150px;
      height: 40px;
      box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
      background-color: var(--text-color);
      color: var(--light-color);
    }
    &__icon-box{
      margin-top: 5px;
      border-radius: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color:var(--text-color);
      min-width: 30px;
      height: 30px;
      margin-left: 10px;
    }
    &__icon {
      transform: rotate(45deg);
      border-radius: 100%;
      display: inline-block;
      fill: #fff;
      width: 20px;
      height: 20px;
      background-image: url("../img/svg/cloze.svg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      transition: transform 0.3s ease-out;
      padding-left: 15px;
    }

    &__box{
      border-radius: 5px;
      border-bottom: dashed 1px #006699;
      box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
      padding: 15px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__btns-box{
      display: flex;
      align-items: center;
      gap: 20px;
    }
    }
  .open .accordion__control{
    border-bottom: none;
  }

  .open .accordion__icon {
    transform: rotate(90deg);
  }

  .open .accordion__content {
    opacity: 1;
    padding-bottom: 18px;}
}
@media (max-width: (1250px)){
  .accordion{
   margin: 0;
  }
  .hero__right{
   min-height: 600px;
  }
  .hero__inner{
    flex-direction: column-reverse;
    align-items: center;
  }
}
@include tablet{
  .hero__inner{
    padding-top: 180px;
    gap: 0;
    padding-bottom: 50px;
  }
  .hero__img{
    position:absolute ;
    bottom: 0;
    left: 0px;
  }
  .hero__descr{
    position: absolute;
    top: 180px;
    right: 100px;
  }
}
@include small-tablet{
  .hero__right{
    min-height: 630px;
   }
  .hero__name{
    margin: 0;
    font-family: 'sans-serif';
    font-style: italic;
    font-size: 30px;
    line-height: 30px;
    margin-left: 0px;
    text-align: center;
    padding-bottom: 0;
  }
  .hero__text{
    display: none;
    margin: 0;
    font-size: 14px;
    font-weight: 500;
    margin-bottom: -5px;
    text-align: center;
    padding-left: 0px;
  }
  .hero__title{
    margin: 0;
    font-size: 30px;
    text-align: center;
  }
  .hero__subtitle{
    display: none;
    padding-left: 0px;
    margin-left: 0;
    text-align: center;
    font-size: 14px;
    font-weight: 500;
    margin-top: -5px;
    margin-bottom: 20px;
  }
  .hero__descr{
    padding: 0px 20px;
    top: 120px;
    right: 0px;
    height: 40px;
  }
  .accordion__box{
    padding: 5px;
  }
  .accordion__title{
    font-size: 16px !important;
  }
  .accordion__descr{
    font-size: 14px !important;
  }
  .hero__btn{
    font-size: 16px;
  }
}
