.graph-modal__close{
  background-repeat: no-repeat;
  font-weight: 400;
  font-size: 18px;
  line-height: 100%;
  width: auto;
  height: auto;
  right: 20px;
  top: 20px;
  color: #000;
  background-image: none;

}

.price__container{
  border-radius: 8px;
  max-width: 1300px;
  border: 1px solid var(--text-color);
}
.price__title{
  font-size: 30px;
  text-align: center;
}
.price__text{
  margin: 0;
  text-align: center;
  padding: 10px 0px;
  background-color: var(--text-color);
  color: #fff;
  text-transform: uppercase;
}

.price__descr{
  padding: 0px 10px;
  font-size: 18px;
  font-weight: 500;
  color: #000;
  text-decoration: underline;

  span{
    font-weight: 700;
  }
}
.price__about{
  margin: 0 auto;
  max-width: 800px;
  text-align: center;
  padding: 10px 0px;
  font-size: 16px;
  font-weight: 500;
  text-decoration: underline;

}

@include small-tablet{
  .graph-modal__close{
    font-size: 12px;
    line-height: 160%;
  }
  .price__title{
    font-size: 20px;
    text-align: center;
  }
  .price__text{
    text-align: center;
  }

  .price__descr{
    font-size: 12px;
    font-weight: 500;
    color: #000;

    span{
      font-weight: 700;
    }
  }
  .price__about{
    margin: 0;
    padding: 10px;
    font-size: 12px;
  }
}

