.about{

  &__container{
    padding-top: 50px;
    background-image: url('../img/bg-main.jpg');
    background-size: cover;
    padding-bottom: 100px;
  }
  &__counter{
    max-width: 800px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
  &__count-box{
    max-width: 280px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
  &__counter-value{
    margin: 0;
    padding: 5px;
    font-size: 50px;
    &::after{
      content: '+';
    }
  }
  &__count-text{
    margin: 0;
    text-align: center;
    font-weight: 600;
    font-size: 16px;
  }
  &__inner{
    margin: 0 auto;
    max-width: 1000px;
    display: flex;
    justify-content: center;
    gap: 40px;
    padding: 10px;
  }
  &__title{
    margin: 0 auto;
    text-align: center;
    font-family: 'sans-serif';
    font-style: italic;
    font-size: 40px;
    font-weight: 400;
    margin-bottom: 30px;
    max-width: 296px;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('../img/btn.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

  &__name{
    margin: 0;
    font-family: 'sans-serif';
    font-style: italic;
    font-size: 40px;
    font-weight: 500;
    padding-bottom: 10px;
  }
  &__right{
    max-width: 600px;
  }

  &__img{
    border-radius: 8px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
    object-fit: contain;
    width: 400px;
    height: 500px;
    transition:  transform 0.5s ease-in;

    @include hover{
      transform: translateY(-10px);
    }
  }
  &__descr{
    margin: 0;
    max-width: 700px;
    font-weight: 400;
    font-size: 22px;
    font-weight: 500;
    line-height: 28px;
    padding-bottom: 10px;
  }

}

@include tablet {
  .about__container{
    padding-bottom: 30px;
  }
  .about__counter-value{
    margin: 0;
    padding: 5px;
    font-size: 40px;
  }
  .about__count-text{
    font-size: 12px;
    max-width: 250px;
  }
  .about__title{
    font-size: 30px;
  }
  .about__name{
    font-size: 30px;
  }
  .about__descr{
    max-width: 500px;
    font-weight: 400;
    font-size: 16px;
    font-weight: 500;
    line-height: 26px;
    padding-bottom: 5px;
  }
  .about__inner{
    flex-direction: column-reverse;
    align-items: center;
  }
}
@include mobile{
  .about__counter{
    flex-direction: column;
    align-items: center;
  }
  .about__img{
    width: 300px;
    height: 350px;
  }
}
