.service{

&__container{
  background-image: url('../img/bg-main.jpg');
  background-size: cover;
  padding-bottom: 80px;
}
&__inner{
  margin: 0 auto;
  max-width: 1300px;
  display: flex;
  justify-content: space-between;
}
&__box {
  display: flex;
  flex-direction: column;
  position: relative;
  min-height: 750px;
}
&__title-box{
  padding-top: 50px;
  margin: 0 auto;
  max-width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
&__icon{
  width: 50px;
  height: 50px;
}
&__title{
  margin: 0 auto;
  text-align: center;
  font-family: 'sans-serif';
  font-style: italic;
  font-size: 40px;
  font-weight: 400;
  margin-bottom: 10px;
}
&__subtitle{
  margin: 0 auto;
  padding-top: 10px;
  max-width: 600px;
  text-align: center;
  font-family: 'sans-serif';
  font-style: italic;
  font-size: 24px;
  font-weight: 400;
  margin-bottom: 30px;
}

.slider {
  overflow: hidden;
  position: relative;
  margin-top: 50px;
  width: 1300px;
  height: 560px;

}

.slider-img{
  width: 400px;
  height: 500px;
  border-radius: 8px;
}
.swiper-slide{
  display: flex;
  gap: 40px;
}
.swiper-img-box{
  background-size: cover;
  border-radius: 5px;
  max-width: 50%;
  border-radius: 8px;
}
.swiper-text{
  max-width: 500px;
  font-weight: 400;
  font-size: 22px;
  font-weight: 500;
  line-height: 28px;
  span{
    font-size: 30px;
    font-weight: 700;
  }
}
.button-prev{
  position: absolute;
  top: 45%;
  left: auto;
  background-color: #D9E454;
  width: 50px;
  height: 50px;
  background-image: url('../img/svg/next.svg');
  background-repeat: no-repeat;
  background-position: center;
  z-index: 100;

  &::after{
    display: none;
  }
}
.button-next{
  position: absolute;
  top: 45%;
  left: 1230px ;
  border: none;
  margin-right: 15px;
  background-color: #D9E454;
  width: 50px;
  height: 50px;
  background-image: url('../img/svg/prev.svg');
  background-repeat: no-repeat;
  background-position: center;
  z-index: 100;

&::after {
  display: none;
  }
}
.swiper-pagination-bullet{
  width: 20px;
  height: 20px;
  background-color: var(--text-color);
  }

}

@media (max-width: (1250px)){
  .slider{
    margin: 0 auto;
    width: 820px !important;
    height: 600px !important;
  }
  .button-next{
    position: absolute;
    top: 45%;
    left: 770px !important;
  }
}
@include tablet{
  .service__title-box{
    gap: 0;
  }
  .service__title{
    font-size: 30px;
  }
  .service__icon{
    width: 40px;
    height: 40px;
  }
}
@media (max-width: (850px)){
  .service__container{
    padding-bottom: 30px;
  }
  .slider{
    margin: 0 auto;
    width: 400px !important;
    height: 600px !important;
  }
  .button-next{
    position: absolute;
    top: 40% !important;
    left: 350px !important;
  }
  .button-prev{
    position: absolute;
    top: 40% !important;
  }
}
@include mobile{
  .slider{
    margin: 0 auto;
    width: 300px !important;
    height: 450px !important;
  }
  .button-prev{
    position: absolute;
    top: 40%;
    left: auto;
    width: 30px !important;
    height: 30px !important;

  }
  .button-next{
    position: absolute;
    left: 270px !important;
    width: 30px !important;
    height: 30px !important;
  }
  .slider-img{
    width: 300px !important;
    height: 350px !important;
  }
  .swiper-pagination-bullet{
    width: 10px !important;
    height: 10px !important;
    background-color: var(--text-color);
    }
}

